'use client';

import React, { useState, ChangeEvent } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Alert, AlertDescription } from '@/components/ui/alert';

type RowData = {
  fileName: string;
  status: string;
  vendorName: string;
  documentType: string;
  poNumber: string;
  billNumber: string;
  billDate: string;
  totalAmount: string;
  projectName: string;
  signed: string;
  detailsOrError: string;
  companyNameVerified: string;
};

export default function HomePage() {
  const [files, setFiles] = useState<FileList | null>(null);
  const [rows, setRows] = useState<RowData[]>([]);
  const [loading, setLoading] = useState<boolean>(false);
  const [error, setError] = useState<string>('');

  const handleFileChange = (e: ChangeEvent<HTMLInputElement>) => {
    setFiles(e.target.files);
    setError('');
  };

  const handleUpload = async () => {
    if (!files || files.length === 0) {
      setError('Please select one or more files.');
      return;
    }

    setError('');
    setLoading(true);
    setRows([]);

    const formData = new FormData();
    Array.from(files).forEach((file) => {
      formData.append('file', file);
    });

    try {
      const res = await fetch('/api/extractdata', {
        method: 'POST',
        body: formData,
      });

      const data = await res.json();
      
      if (!res.ok) {
        throw new Error(data.error || 'Failed to process files');
      }

      if (data.error) {
        setError(data.error);
      } else {
        setRows(data.rows || []);
      }
    } catch (err: any) {
      setError(err.message || 'Error uploading files.');
    } finally {
      setLoading(false);
    }
  };

  const updateSignedStatus = (index: number, value: string) => {
    const updatedRows = [...rows];
    updatedRows[index].signed = value;
    setRows(updatedRows);
  };

  return (
    <div className="min-h-screen bg-background p-4">
      <div className="max-w-7xl mx-auto space-y-6">
        {/* Header */}
        <div className="text-center space-y-2">
          <h1 className="text-4xl font-bold text-foreground">Document Data Extractor</h1>
          <p className="text-muted-foreground text-lg">
            Upload images or PDFs to extract and organize document data
          </p>
        </div>

        {/* Upload Section */}
        <Card>
          <CardHeader>
            <CardTitle>Upload Files</CardTitle>
            <CardDescription>
              Select multiple images (JPG, PNG) or PDF files for data extraction
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="space-y-2">
              <Input
                type="file"
                multiple
                accept="image/*,.pdf"
                onChange={handleFileChange}
                className="cursor-pointer"
              />
              {files && files.length > 0 && (
                <p className="text-sm text-muted-foreground">
                  {files.length} file(s) selected
                </p>
              )}
            </div>
            
            <Button 
              onClick={handleUpload} 
              disabled={loading || !files || files.length === 0}
              className="w-full sm:w-auto"
            >
              {loading ? 'Processing...' : 'Extract Data'}
            </Button>
          </CardContent>
        </Card>

        {/* Error Alert */}
        {error && (
          <Alert variant="destructive">
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}

        {/* Loading State */}
        {loading && (
          <Card>
            <CardContent className="p-6">
              <div className="flex items-center justify-center space-x-2">
                <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-primary"></div>
                <span>Processing files...</span>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Results Table */}
        {rows.length > 0 && (
          <Card>
            <CardHeader>
              <CardTitle>Extracted Data</CardTitle>
              <CardDescription>
                Review and edit the extracted information. You can manually update the "Signed?" status.
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="overflow-x-auto">
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead className="min-w-[120px]">File Name</TableHead>
                      <TableHead>Status</TableHead>
                      <TableHead className="min-w-[150px]">Vendor Name</TableHead>
                      <TableHead>Document Type</TableHead>
                      <TableHead>PO Number</TableHead>
                      <TableHead>Bill Number</TableHead>
                      <TableHead>Bill Date</TableHead>
                      <TableHead>Total Amount</TableHead>
                      <TableHead className="min-w-[120px]">Project Name</TableHead>
                      <TableHead>Signed?</TableHead>
                      <TableHead className="min-w-[200px]">Details/Error</TableHead>
                      <TableHead>Company Verified</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {rows.map((row, idx) => (
                      <TableRow 
                        key={idx} 
                        className={row.status === 'error' ? 'bg-destructive/10' : ''}
                      >
                        <TableCell className="font-medium">{row.fileName}</TableCell>
                        <TableCell>
                          <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${
                            row.status === 'success' 
                              ? 'bg-green-100 text-green-800' 
                              : 'bg-red-100 text-red-800'
                          }`}>
                            {row.status}
                          </span>
                        </TableCell>
                        <TableCell>{row.vendorName}</TableCell>
                        <TableCell>{row.documentType}</TableCell>
                        <TableCell>{row.poNumber}</TableCell>
                        <TableCell>{row.billNumber}</TableCell>
                        <TableCell>{row.billDate}</TableCell>
                        <TableCell>{row.totalAmount}</TableCell>
                        <TableCell>{row.projectName}</TableCell>
                        <TableCell>
                          <Select
                            value={row.signed}
                            onValueChange={(value) => updateSignedStatus(idx, value)}
                          >
                            <SelectTrigger className="w-20">
                              <SelectValue />
                            </SelectTrigger>
                            <SelectContent>
                              <SelectItem value="Yes">Yes</SelectItem>
                              <SelectItem value="No">No</SelectItem>
                            </SelectContent>
                          </Select>
                        </TableCell>
                        <TableCell className="max-w-[200px]">
                          <div className="truncate" title={row.detailsOrError}>
                            {row.detailsOrError}
                          </div>
                        </TableCell>
                        <TableCell>
                          <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${
                            row.companyNameVerified === 'Yes' 
                              ? 'bg-green-100 text-green-800' 
                              : 'bg-gray-100 text-gray-800'
                          }`}>
                            {row.companyNameVerified}
                          </span>
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Instructions */}
        <Card>
          <CardHeader>
            <CardTitle>Instructions</CardTitle>
          </CardHeader>
          <CardContent className="space-y-2 text-sm text-muted-foreground">
            <p>• Upload multiple images (JPG, PNG, BMP, TIFF) or PDF files</p>
            <p>• The system will extract text using OCR technology</p>
            <p>• Company names are verified against: "dev accelerator limited" and "needle and thread llp"</p>
            <p>• You can manually update the "Signed?" status for each document</p>
            <p>• Processing may take a few moments depending on file size and quantity</p>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
